
@font-face {
   font-family:Antonio ;
   src: url(./fonts/Antonio-Light.ttf);
}
@font-face {
   font-family:jerindemo;
   src: url(./fonts/jerindemo.ttf);
}
@font-face {
   font-family:Tusker;
   src: url(./fonts/TuskerGrotesk-2800Super.otf);
}
@font-face {
   font-family:Tusker2;
   src: url(./fonts/TuskerGrotesk-3500Medium.ttf);
}

$bg-color: (
   "Dimblack" : #181818,
   // "white" : #161817,
   "white" : ghostwhite,
   "empty" : transparent,
   "teal" : teal,
   "red" : red,
   "heigh" : #cfbd99,
   "light": #08323c
);

$F-colors: (
   "Dimblack" : #181818,
   "lightblack" : #181818a8,
   "white" : ghostwhite,
   "lightgrey" : #f8f8ffcd,
   "red" : red,
   "olive" : #686826,
   "empty" : transparent
);


@mixin F-sizes($size,$name, $space){
   font-size: $size;
   text-transform: uppercase;
   font-family: $name;
   letter-spacing: $space;
}

@mixin display-flex($direction,$position, $base, $gap,$grow) {
   display: flex;
   justify-content: $position;
   flex-flow: $direction;
   flex-basis: $base;
   gap: $gap;
   flex-grow: $grow;
}

@mixin maxwidth($fill,$maxcontent){
   width: $fill;
   height: $maxcontent;
}

@mixin linkdecor{
   text-decoration: none;
   color:black;
}


html > body {
   padding: 0;
   margin: 0;
   @include maxwidth(100%, 100vh);
   background:map-get($bg-color, "Dimblack" );
   box-sizing: border-box;

   &::-webkit-scrollbar {
      display: none;
   }
}

.kids-section {
 section[id="section1"]{
   @include maxwidth(100%, max-content);
   background:map-get($bg-color, "white" );
   box-sizing: border-box;
   @include display-flex(row wrap,flex-start , 100%,0px , 0);


   header {
      padding: 10px;
      @include maxwidth(100%, max-content);
      background:map-get($bg-color, "Dimblack" );
      & > .nav-wrapper {
         @include maxwidth(100%, 50px);
         padding: 0 2rem;
         @include display-flex(row nowrap,space-between , 100%,1px , 0);
         background:map-get($bg-color, "empty" );
         box-sizing: border-box;


         ul[data-nav="menu"] li {
            padding-top: 5px;
            box-sizing: border-box;
            @include display-flex(column,flex-start, 100%,5px , 0);


            & > .MENU {
               display: block;
               width: 59px;
               border-bottom: 1px solid ghostwhite;
            }
            .cover{
               position: relative;
               width: 59px;
               height: 30px;
               background: map-get($bg-color, "empty" );
               top: -30px
            }
         }

         ul[data-nav="links"] {
          @include display-flex(row nowrap,flex-end, 100%,35px , 0);

           & > [data-light="icon"]{
            background: url(./icon/iconmonstr-idea-6-240.png)no-repeat center center / cover border-box;
            padding: .70rem;

           }
           & > [data-bag="icon"]{
            background: url(./icon/iconmonstr-briefcase-thin-240.png)no-repeat center center / cover border-box;
            padding: .70rem;
           }

           & > .off {
            position: absolute;
            width: 50px;height:30px;
            background: transparent;
            right: 4.9rem;
            top: 20px;
            display: block;
            // display: none;
            z-index: 8888;
            cursor: pointer;
         }
         }

         .middle-menu {
            @include display-flex(row nowrap,flex-start, 100%,45px , 0);
            & > li{
               list-style: none;
               a {
                  @include F-sizes(15px,Antonio , 0px);
                  cursor: pointer;
                  text-decoration: none;
                  flex-basis: 100%;
                  color: map-get($F-colors, "white" );

                  &:hover {
                     color: #b8b5ae;
                     transition: .2s linear;
                  }
               }
            }
         }


         ul > li {
            list-style: none;
            color: map-get($F-colors, "white" );
            @include F-sizes(15px,Antonio , 0px);
            cursor: pointer;
         }

         ul[data-nav="logo"]  {
            background: url(./assets/denim/logo.png)no-repeat center center / contain border-box;
            width: 400px;
            height: 400px;
            background:ghostwhite;
            z-index: 99999;
            display: none;


            & > a {
               list-style: none;
               text-decoration: none;
               color: map-get($F-colors, "Dimblack" );
               cursor: pointer;
               & > li {
                  background: url(./assets/denim/logo.png)no-repeat center center / contain border-box;
                  width: 500px;
                  height: 400px;

               }
               p{
                  // background: red;
                  position: relative;
                  top: -150px;
                  @include F-sizes(16px,times , 0px);
                  text-transform:none;
                  color: map-get($F-colors, "black" );

                  & > small {
                     @include F-sizes(15px,times , 0px);
                     font-style: italic;
                  }

                  & > .para {
                     @include F-sizes(18px,times , 0px);
                     color: map-get($F-colors, "black" );
                     & > small {
                        @include F-sizes(15px,times , 0px);
                        font-style: italic;
                     }
                  }

                  .noun {
                     @include F-sizes(15px,times , 0px);
                     text-transform: lowercase;
                     font-style: italic;
                  }
               }
            }


         }


         .cloth-filter {
            padding: 0rem 2.6rem;
            box-sizing: border-box;
            @include maxwidth(100%, max-content);
            background:map-get($bg-color, "white" );
            @include display-flex(row nowrap,flex-end, 100%,35px , 0);
            & > .filter-wrapper , .filter-icon{
               background:map-get($bg-color, "empty" );
               padding: 6px 20px;
               box-sizing: border-box;
               border: 1px solid ghostwhite;
               cursor: pointer;

               & > .filter {
                  display: block;
                  color: map-get($F-colors, "white" );
                  @include F-sizes(16px,Antonio , 0px);
               }

            }
         }
      }
   }



   .steppara-banner {
      align-items: center;
      width: 100%;
      height: 720px;
      background:map-get($bg-color, "empty" );
      position: relative;
      background: url(./assets/kids/a80c1f165903723.640f809246d69.jpg) no-repeat center center / cover border-box;

      .banner {
         @include maxwidth(100%,700px);
         background:map-get($bg-color, "empty" );
         @include display-flex(row wrap,flex-start, 100%,35px , 0);
         align-items: flex-end;
         z-index: 100;
         position: relative;



         p {
            padding-left: 2rem;
            flex-basis: 50%;
            color: map-get($F-colors, "black" );
            @include F-sizes(15px,Antonio , 0px);
            color:#ffffff;
            position: relative;
            z-index: 100;
            max-width: 600px;
            line-height: 1.9em;
         }

      }

   }


   .cloth-info {
      @include maxwidth(100%,max-content);
      background:map-get($bg-color, "Dimblack" );
      padding:5rem;
      box-sizing: border-box;
      color: ghostwhite;


      & > span {
         display: block;
         @include F-sizes(16px,Antonio , 0px);
      }
      p {
         @include F-sizes(16px,Antonio , 0px);
         word-spacing: 2px;
         max-width: 640px;
      }
   }

   .long-sleeve {
      @include maxwidth(100%,max-content);
      background:map-get($bg-color, "Dimblack" );
      @include display-flex(row wrap,center, 100%,0 , 0);
      & > .sleev-info {
         @include maxwidth(100%,max-content);
         background:map-get($bg-color, "Dimblack" );
         padding:5rem 5rem;
         box-sizing: border-box;
         color: ghostwhite;
         flex-basis: 100%;


         & > span {
            display: block;
            @include F-sizes(16px,Antonio , 0px);
         }
         p {
            @include F-sizes(16px,Antonio , 0px);
            word-spacing: 2px;
            max-width: 600px;
         }
      }

      .sleev-rows {
         flex-basis: 100%;
         padding-left: 4rem;
         padding-bottom: 5rem;
      }

   }

   .sec-1 {
      @include maxwidth(100%,max-content);
      @include display-flex(row wrap,flex-start, 100%,0 , 0);
      background:map-get($bg-color, "white" );
      padding-bottom: 5rem;

      & > h4 {
         flex-basis: 100%;
         text-align: center;
         @include F-sizes(20px,Antonio , 0px);
         color: black;
         font-weight: 100;
      }
      .img-wrap, .img-wrap-3 {
         @include maxwidth(100%,max-content);
         flex-basis: 100%;
         @include display-flex(row wrap,center, 100%,2rem , 0);
         .img-sec1 {
            object-fit: cover;
            object-position: center;
            max-width: 100%;
            height: 720px;
            cursor: pointer;
         }
         & > span {
            @include F-sizes(16px,Antonio , 0px);
            color: black;
            display: block;
         }
      }
      .img-wrap-3 {
         @include display-flex(row wrap,center, 100%,2rem , 0);
      }

   }
   .sec-2,.sec-3{
      @include maxwidth(100%,max-content);
      @include display-flex(row wrap,center, 100%,0 , 0);
      background:map-get($bg-color, "white" );

      .img-wrap-3{
         background:ghostwhite;
         @include maxwidth(100%,max-content);
         padding: 2rem;
         flex-basis: 100%;
         @include display-flex(row wrap,center, 100%,1rem , 0);

         .img-sec1 {
            object-fit: cover;
            object-position: center;
            max-width: 34%;
            height:50%;
            cursor: pointer;
         }
         & > span {
            @include F-sizes(16px,Antonio , 0px);
            color: black;
            display: block;
         }
      }

   }


   main[id="main-content-column"],#content-column{
      @include maxwidth(100%,max-content);
      background:map-get($bg-color, "white" );
      box-sizing: border-box;
      overflow-x: hidden;
      position: relative;
      overflow-y: hidden;
      padding: 6rem 3rem;



      .cloth-section {
         @include maxwidth(100%, max-content);
         background:map-get($bg-color, "white" );
         display: grid;
         grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
         gap: 1rem;

         position: relative;
         z-index: 33333;
         overflow: hidden;


         & > .col1{
            @include maxwidth(100%, max-content);

            .kids {
               @include maxwidth(100%, max-content);
               padding: 1rem;
               display: flex;
               position: relative;
               overflow: hidden;




               & > .img {
                  max-width: 100%;
                  height: 400px;
                  scroll-snap-align: start;
                  scroll-snap-stop: always;
                  cursor: pointer;
               }

            }

            .H-scroll{
               display: grid;
               grid-template-columns: repeat(3,500px);
               justify-content: flex-start;
               box-sizing: border-box;
               overflow-x:  scroll;
               scroll-snap-align: start;
               scroll-snap-type: x mandatory;
               scroll-padding-inline-start: 3rem;
               scroll-behavior: smooth;
               padding-left: 1rem;
               padding-right: 10rem;
               box-sizing: border-box;
               gap: 12rem;

               &::-webkit-scrollbar {
                  height: .30vh;
               }
               &::-webkit-scrollbar-thumb {
                 background: map-get($bg-color, "empty" );
               }
               &:hover::-webkit-scrollbar-thumb {
                  background: map-get($bg-color, "Dimblack" );
                }

            }
            .H_scroll_woman {
                 @include maxwidth(100%, max-content);
                  display: grid;
                  grid-template-columns: repeat(3,500px);
                  justify-content: flex-start;
                  box-sizing: border-box;
                  overflow-x:  scroll;
                  scroll-snap-align: start;
                  scroll-snap-type: x mandatory;
                  scroll-padding-inline-start: 3rem;
                  scroll-behavior: smooth;
                  padding-left: 6rem;
                  padding-right: 10rem;
                  box-sizing: border-box;
                  gap: 12rem;

                  &::-webkit-scrollbar {
                     height: .30vh;
                  }
                  &::-webkit-scrollbar-thumb {
                    background: map-get($bg-color, "empty" );
                  }
                  &:hover::-webkit-scrollbar-thumb {
                     background: map-get($bg-color, "Dimblack" );
                   }

                   #women-video22 {
                     width: 465px;
                     height:700px;
                     object-fit: cover;
                     object-position: center;
                     scroll-snap-align: start;
                     scroll-snap-stop: always;
                     cursor: pointer;
                 }

            }

            & > .cloth-name-price {
               @include maxwidth(100%, max-content);
               @include display-flex(row ,flex-start, 100%,20px , 0);
               padding: 0 1rem;
               box-sizing: border-box;
               flex-basis: 100%;
               background:map-get($bg-color, "white" );
               // padding-inline-end: 10rem;

               & > span {
                  @include F-sizes(16px,Antonio , 0px);
               }
            }
         }


      }
   }



   .bomber-row {
      @include maxwidth(100%, max-content);
      display: grid;
      grid-template-columns: repeat(6,600px);
      justify-content: flex-start;
      box-sizing: border-box;
      overflow-x:  scroll;
      scroll-snap-align: start;
      scroll-snap-type: x mandatory;
      gap: 0rem;
      padding-right: 1rem;
      background:map-get($bg-color, "Dimblack" );

      .col1 {
         padding-left: 4rem;
         background:map-get($bg-color, "Dimblack" );
         padding: 5rem 4rem;


         .H-scroll {
            background:map-get($bg-color, "Dimblack" );

            .img {
               max-width: 100%;
               height: 600px;
               scroll-snap-align: start;
               scroll-snap-stop: always;
               scroll-padding-inline-start: 4rem;
            }
         }
         & > .cloth-name-price {
            @include maxwidth(100%, max-content);
            @include display-flex(row ,flex-start, 100%,70px , 0);
            box-sizing: border-box;
            flex-basis: 100%;
            background:map-get($bg-color, "Dimblack" );
            // padding-inline-end: 10rem;

            & > span {
               @include F-sizes(16px,Antonio , 0px);
               color: ghostwhite
            }
         }
      }
   }
 }

 .animate{
   height: max-content;
   background:map-get($bg-color, "light" );
   overflow: hidden;
   & > h1 {
      margin: 0;
      padding: 0;
      white-space: nowrap;
      @include F-sizes(2rem,Tusker2 ,3px );
      color: map-get($F-colors, "white" );
      animation: infinit 60s linear infinite;
      transition: animation .10s linear;
      font-weight: 400;

      & > .spin {
         display: inline-block;
         animation: spin 10s linear infinite;
         transition: animation .5s linear;
         @include maxwidth(40px,46px )

      }


     @keyframes spin {

      from {
         transform: rotate(0);
      }
      to {
         transform: rotate(360deg);
      }

     }
   }

   @keyframes infinit {

      from {
         transform: translate(0);
      }
      to {
         transform: translate(-2900px);
      }

   }

}


.footer-wrapper {
   @include maxwidth(100%,100vh);
   box-sizing: border-box;
   position: relative;
   flex-basis: 100%;
   & > .footer{
      @include maxwidth(100%,100vh);
      background: map-get($bg-color, "Dimblack" );
      @include display-flex(row wrap,flex-start ,100% ,0px ,1 );
      box-sizing: border-box;
      overflow: hidden;
      & > .footer1 {
         flex-basis: 100%;
         background: map-get($bg-color, "Dimblack" );
      }


      .copyright{
         @include maxwidth(100%, max-content);
         background: map-get($bg-color, "Dimblack" );
         box-sizing: border-box;
         padding-top: 3rem;
         padding-bottom: 10rem;

         & > ul {

            & > li {
               margin-bottom: 5px;
               list-style: none;
               font-weight: 400;
               @include F-sizes(14px,Antonio ,0px);
               @include linkdecor;
               white-space: nowrap;
               color: map-get($F-colors,"white");
            }
         }

      }

      .yearLogo {
         @include display-flex(row wrap,center ,100% ,0px ,1 );
         @include maxwidth(100%, max-content );
         & > .year {
            position: relative;
            display: inline-block;
            font-weight: 400;
            @include F-sizes(14px,Antonio ,0px);
            color: map-get($F-colors,"white" );
         }

      }


   }
}


}

