               @include F-sizes(17px,Antonio , 0px);
               color: ghostwhite;
               font-weight: 100;
            }
         }
         #main-content-column {

            .cloth-section {
               grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
               gap: 5px;
               justify-content: space-between;
               padding-left: 0rem;


               & > .col1{
                  @include maxwidth(100%, max-content);
                  .woman1 {

                     & > .img {
                        max-width: 100%;
                        height: 300px;
                        scroll-snap-align: start;
                        scroll-snap-stop: always;
                        cursor: pointer;
                     }

                  }

                  .H-scroll{
                     grid-template-columns: repeat(3,500px);
                     scroll-padding-inline-start: 0rem;
                     padding-left: 0rem;
                     padding-right: 1rem;
                     box-sizing: border-box;
                     gap: 1rem;
                  }

                  .H_scroll_woman {
                      scroll-padding-inline-start: 0rem;
                       #women-video22 {
                        width: 200px;
                         height:300px;

                     }

                    }

                  & > .cloth-name-price{
                     justify-content: flex-start;
                     gap: 8px;
                     padding: 0 0rem;
                     & > span {
                        @include F-sizes(12px,Antonio , 0px);
                     }
                  }


               }
            }
         }
      }
   }
}








