body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 1200px;
  background: #181818;
  box-sizing: border-box;
  position: relative;
  display: grid;
  grid-template-columns: 100% 100%;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  display: none;
}

.mobile-menu {
  display: none;
}

main[id=main_header] {
  width: 100%;
  height: -moz-max-content;
  height: max-content;
  background: transparent;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  bottom: 0;
}
main[id=main_header] header[id=header] {
  position: relative;
  width: 100%;
  height: 100vh;
  background: transparent;
  z-index: 1300;
}
main[id=main_header] header[id=header] .header-wrapper {
  width: 100%;
  height: -moz-max-content;
  height: max-content;
  background: transparent;
  padding: 1rem 1rem;
  box-sizing: border-box;
  position: relative;
  z-index: 3000;
}
main[id=main_header] > .video-wrapper {
  width: 100%;
  height: 100%;
  background: transparent;
  position: absolute;
  top: 0;
  z-index: 1;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}
main[id=main_header] > .video-wrapper > video[id=video] {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center;
     object-position: center;
  z-index: 1;
}

#menu {
  width: 515px;
  height: 100vh;
  background: ghostwhite;
  box-sizing: border-box;
  position: fixed;
  z-index: 199900;
  transform: translateX(-400rem);
  display: none;
  z-index: 1000000;
}
@keyframes menu-open {
  from {
    opacity: 0;
  }
  to {
    transform: translateX(0);
    display: block;
    opacity: 1;
  }
}
@keyframes menu-x {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateX(-40rem);
    opacity: 0;
  }
}
#menu .menu-lists {
  width: 100%;
  height: -moz-max-content;
  height: max-content;
  display: flex;
  justify-content: flex-end;
  flex-flow: column nowrap;
  flex-basis: 100%;
  gap: 4px;
  flex-grow: 1;
  align-items: flex-end;
  padding: 5rem 2rem 10px 2rem;
  box-sizing: border-box;
  overflow-x: scroll;
}
#menu .menu-lists > .cancel {
  display: block;
  width: 59px;
  border-bottom: 1px solid black;
  cursor: pointer;
  background: black;
  padding: 0.2rem;
}
#menu .menu-lists .cover-cancel {
  position: relative;
  width: 59px;
  height: 52px;
  background: transparent;
  top: -41px;
  cursor: pointer;
}
#menu .menu-lists .rotate1 {
  transform: rotate(-45deg);
}
#menu .menu-lists .rotate4 {
  transform: rotate(45deg) translateY(-4px);
}
#menu .mobile {
  width: 100%;
  height: -moz-max-content;
  height: max-content;
  background: ghostwhite;
  display: flex;
  justify-content: flex-start;
  flex-flow: row nowrap;
  flex-basis: 100%;
  gap: 0rem;
  flex-grow: 0;
  padding: 0px 30px;
  box-sizing: border-box;
  transform: translateX(-100rem);
  position: absolute;
  z-index: 8888;
}
#menu .mobile::-webkit-scrollbar {
  width: 2px;
}
#menu .mobile::-webkit-scrollbar-thumb {
  background: orangered;
}
@keyframes menu-links {
  from {
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
#menu .mobile > ul {
  padding: 2px 0;
  box-sizing: border-box;
  flex-basis: 100%;
}
#menu .mobile > ul #menu-links {
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
}
#menu .mobile > ul #menu-links > li {
  list-style: none;
  font-weight: 100;
  font-size: 15px;
  color: #181818;
  text-transform: uppercase;
  font-family: Antonio;
  letter-spacing: 0px;
  text-decoration: none;
  color: black;
  font-weight: 100;
  white-space: nowrap;
  margin-bottom: 15px;
  cursor: pointer;
}
#menu .mobile > ul #menu-links > li:hover {
  transform: translateX(7px);
  transition: all 0.3s linear;
  color: #787777;
}
#menu .mobile > ul #menu-links::-webkit-scrollbar {
  width: 2px;
}
#menu .mobile > ul #menu-links::-webkit-scrollbar-thumb {
  background: #181818;
}

